import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Thread, Collections, ThreadCategory } from '../../app/app.models';
import { AngularFirestore } from 'angularfire2/firestore';
import { ViewController } from 'ionic-angular/navigation/view-controller';
import { DataProvider } from '../../providers/data/data';

@IonicPage()
@Component({
  selector: 'page-thread-add',
  templateUrl: 'thread-add.html',
})
export class ThreadAddPage {

  public thread: Partial<Thread> = {};
  public cat: ThreadCategory;
  public cat_id: string = this.navParams.get('cat');

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore,
    private viewCtrl: ViewController,
    private data: DataProvider,
  ) {
  }

  async addThread() {
    this.cat = await this.data.getDocAlt<ThreadCategory>(Collections.THREAD_CATEGORY, this.cat_id);
    this.thread.id = this.afs.createId();
    this.thread.date = new Date().toISOString();
    this.thread.cat_id = this.cat_id;
    this.thread.cat_name = this.cat.name;
    await this.afs.collection(Collections.THREAD).doc(this.thread.id).set({...this.thread});
    this.viewCtrl.dismiss();
  }

}



